.agree{
  &__title{
    margin-top: 18px;
  }
  &__text{
    margin-top: 18px;
    img{
      display: block;
      margin: 23px 0 26px;
      width: 100%;
    }
  }
  .add-feedback{
    max-width: 675px;
    &__title{
      line-height: 120%;
    }
    .add-feedback__btn{
      grid-column: 1/-1;
    }
  }
  .catalog-item__hint{
    line-height: inherit;
    font-size: inherit;
  }
}

.attention{
  margin-top: 41px;
  max-width: 520px;
  &__desc{
    margin-top: 20px;
  }
  &__item{
    margin-top: 30px;
  }
  .catalog-card__status-item{
    align-items: center;
  }
  .catalog-card__status-icon{
    width: 20px;
  }
}

.owner{
  padding: 40px 0 120px;
  &__title{
    margin-top: 41px;
  }
  &__desc{
    margin-top: 10px;
    max-width: 541px;
  }
  .add-feedback{
    margin-top: 32px;
    max-width: 675px;
    &__btn{
      grid-column: 1/-1;
    }
  }
}


@media screen and (max-width: 992px){
  .agree{
    &__title{
      margin-top: 49px;
    }
    &__text{
      margin-top: 12px;
      font-size: 15px;
    }
    .add-feedback{
      &__title{
        font-size: 20px;
        line-height: 140%;
      }
    }
  }

  .attention{
    margin-top: 61px;
    &__item{
      margin-top: 20px;
    }
    .body-text--big{
      font-size: 15px;
    }
  }

  .owner{
    padding: 30px 0 70px;
  }
}
