.display1{
  font-weight: 700;
  font-size: 45px;
  line-height: 118%;
  font-family: $font2;
}

.display2{
  font-weight: 700;
  font-size: 30px;
  line-height: 150%;
  color: rgba($black,.7);
}

.body-text> h3,
.display3{
  font-weight: 700;
  font-size: 20px;
  line-height: 150%;
  color: rgba($black,.7);
}

.body-text{
  font-size: 14px;
  line-height: 150%;
  color: rgba($black,.7);
  &--big{
    font-size: 15px;
  }
  p,
  ul,
  ol{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 22px;
    }
  }
  strong{
    font-weight: 700;
  }
  li{
    line-height: inherit;
    display: flex;
  }
  ul{
    li{
      &::before{
        content: '';
        display: block;
        width: 5px;
        height: 5px;
        border-radius: 50%;
        background-color: rgba($black,.7);
        transform: translateY(8px);
        margin-right: 5px;
        flex-shrink: 0;
      }
    }
  }
  ol{
    counter-reset: list1;
    li{
      &:before{
        counter-increment: list1;
        content: counter(list1) ". ";
        margin: 0 3px;
      }
    }
  }
  &>h3{
    margin-bottom: 11px;
  }
}

.semi-bold-text{
  font-weight: 600;
}

.bold-text{
  font-weight: 700;
}

.mlist{

}


@media screen and (max-width: 992px){
  .display1{
    font-size: 25px;
    line-height: 120%;
  }

  .body-text{
    font-size: 13px;
    &--big{
      font-size: 14px;
    }
  }
}
