.back-link {
  border-radius: 5px;
  padding: 10px;
  display: inline-block;
  font-weight: 700;
  font-size: 15px;
  line-height: 150%;
  &:hover {
    svg {
      path {
        fill: #ffffff;;
        opacity: 1;
      }
    }
  }
  svg {
    margin-right: 5px;
  }
}

.contacts {
  font-family: $font1;
  padding: 40px 0 100px;
  &__title {
    margin-top: 20px;
  }
  &__items {
    margin-top: 20px;
  }
  &__item {
    display: flex;
    &:not(:first-child) {
      margin-top: 20px;
    }
    &-subtitle {
      font-size: 13px;
      line-height: 150%;
      color: $black;
      opacity: 0.7;
    }
    &-info {
      font-weight: 700;
      font-size: 18px;
      line-height: 150%;
      color: $black;
      opacity: 0.7;
    }
  }
  &__desc {
    margin-left: 15px;
  }
  &__text {
    margin-top: 35px;
  }

}

@media screen and (max-width: 992px) {
  .back-link {
    background: none;
    &:hover {
      background: none;
      color: $black;
      svg {
        path {
          fill: $black;
        }
      }
    }
  }
  .contacts {
    padding: 20px 0 163px;
    &__title {
      font-size: 20px;
      margin-top: 40px;
    }
    &__items {
      margin-top: 33px;
    }
  }
}
