@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import 'components/_fonts.scss';

.tabs{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.tab{
    padding: 0 10px;
    font-size: 15px;
    color: rgba($black,.6);
    height: 38px;
    background: #E0E0E0;
    border-radius: 5px;
    display: flex;
    align-items: center;
    margin: 10px 10px 0 0;
    transition: all .3s;
    cursor: pointer;
    &--active,
    &:hover{
        color: #fff;
        background: $black;
    }
    &--active{
        pointer-events: none;
    }
}

.form-grid{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 12px;
    .fg--text{
        grid-column: 2/3;
        grid-row: 1/4;
        textarea{
            min-height: 100%;
        }
    }
}

.fg {
    position: relative;
    margin: 0;
    input,
    textarea{
        border: none;
        background: #EFEFEF;
        border-radius: 5px;
        padding: 0 20px 0 40px;
        height: 57px;
        width: 100%;
        font-size: 18px;
        color: $black;
        &::placeholder {
            color: rgba($black,.5);
        }
    }
    textarea{
        height: 190px;
        resize: none;
        padding-top: 17px;
        padding-bottom: 17px;
        &+img{
            top: 19.5px;
            transform: unset;
        }
    }
    img{
        position: absolute;
        left: 10px;
        top: 50%;
        transform: translateY(-50%);
    }
}

.mob-visible{
    display: none;
}

.check {
    position: relative;
    input {
        position: absolute;
        opacity: 0;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: 2;
        cursor: pointer;
        &:checked+label {
            &::before {
                // background-color: $green;
                border: 3px solid #fff;
            }
        }
        &:disabled+label {
            color: #AAAAAA;
            pointer-events: none;
        }
    }
    label {
        padding-left: 40px;
        position: relative;
        min-height: 20px;
        font-size: 14px;
        line-height: 20px;
        cursor: pointer;
        &:before {
            content: '';
            position: absolute;
            z-index: 1;
            border-radius: 2px;
            background: rgba(255, 255, 255, 0.01);
            border: 1px solid rgba(255, 255, 255, 0.45);
            width: 20px;
            height: 20px;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            transition: all .100s;
        }
    }
}


@media screen and (max-width: 992px){
    .desktop-visible{
        display: none;
    }

    .mob-visible{
        display: unset;
    }
}


@media screen and (max-width: 768px){
    .form-grid{
        grid-template-columns: 100%;
        gap: 12px;
        .fg--text{
            grid-column: unset;
            grid-row: unset;
        }
    }
}
