.catalog-item{
  padding: 41px 0 107px;
  &__grid{
    display: grid;
    grid-template-columns: calc(69.3% - 41px) 30.7%;
    gap: 41px;
  }
  &__content{
    padding: 25px 25px 50px;
    background: #FFFFFF;
    border-radius: 5px;
  }
  &__banner{
    position: sticky;
    top: 110px;
    display: block;
    img{
      width: 100%;
      display: block;
      border-radius: 5px;
    }
  }
  &__top{
    display: flex;
    align-items: center;
    padding-right: 53%;
  }
  &__promo{
    width: 98px;
    margin-left: 9px;
  }
  &__info{
    margin-top: 3px;
    position: relative;
    padding-right: 53%;
    display: grid;
    grid-template-columns: 130px calc(100% - 140px);
    gap: 15px 10px;
    .catalog-card__status{
      line-height: 20px;
      &-icon{
        width: 20px;
      }
    }
    &-left{
      display: grid;
      grid-template-columns: 130px calc(100% - 140px);
      gap: 15px 10px;
      grid-column: 1/-1;
    }
  }
  &__site{
    padding: 0 15px;
    height: 32.55px;
    font-size: 13.2414px;
    border-radius: 4.13793px;
    svg{
      margin-right: 8px;
    }
    span{
      text-decoration-line: underline;
    }
    &:hover{
      g{
        opacity: 1;
      }
      path{
        stroke: #fff;
      }
    }
  }
  &__rating{
    grid-column: 1/-1;
    display: flex;
    align-items: center;
  }
  &__screen{
    position: absolute;
    width: 53%;
    top: -57px;
    right: 0;
    img{
      width: 100%;
    }
  }
  &__share{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    &-link{
      margin-left: 5px;
      flex-shrink: 0;
    }
  }
  &__hint{
    margin-top: 17px;
    padding: 10px;
    background: rgba(217, 217, 217, 0.33);
    border-radius: 5px;
    font-weight: 300;
    font-size: 10px;
    line-height: 156%;
    color: rgba($black,.7);
    font-style: italic;
  }
  &__card{
    position: relative;
    .catalog-card__place{
      position: absolute;
      width: 50px;
      height: 220px;
      right: 100%;
      top: 0;
      transform: translate(-25px,-25px);
      border-radius: 5px 0px 0px 5px;
      background: linear-gradient(135.8deg, rgba(255, 219, 149, 0.66) -5.12%, rgba(255, 209, 166, 0.66) 75.35%);
      &--red{
        background: linear-gradient(0deg, #DF3737, #DF3737), linear-gradient(135.8deg, rgba(255, 219, 149, 0.66) -5.12%, rgba(255, 209, 166, 0.66) 75.35%);
      }
    }
  }
}

.product-detail{
  margin-top: 14px;
  grid-column: 1/-1;
  &__prop{
    display: flex;
    align-items: center;
    &:not(:last-child){
      margin-bottom: 12px;
    }
  }
  &__icon{
    width: 21px;
    flex-shrink: 0;
    margin-right: 7px;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__info{
    margin-top: 19px;
  }
  &__group{
    &:not(:last-child){
      margin-bottom: 16px;
    }
  }
  &__title,
  &__desc{
    font-size: 12px;
    line-height: 156%;
    opacity: .7;
  }
  &__title{
    display: flex;
    align-items: center;
    img{
      margin-left: 3px;
    }
  }
  &__desc{
    span{
      display: block;
      line-height: inherit;
    }
  }
  &__sites{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 1px;
  }
  &__site{
    margin: 5px 5px 0 0;
    height: 28px;
    font-size: 12px;
    padding: 0 5px;
    border-radius: 5px;
  }
}

.about-service{
  margin-top: 25px;
  &__hint{
    margin-top: 10px;
    background: rgba(223, 55, 55, 0.22);
    border-radius: 5px;
    padding: 10px;
    max-width: 554px;
    font-weight: 700;
    font-size: 15px;
    line-height: 156%;
    color: rgba($red,.7);
  }
  &__text{
    margin-top: 21px;
  }
}

.add-feedback{
  margin-top: 30px;
  &__form{
    margin-top: 17px;
  }
  &__buttons{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 9px;
    margin-top: 5px;
  }
  &__btn{
    height: 48px;
    font-size: 13.2414px;
    border-radius: 4.13793px;
  }
}

.catalog-feedback{
  margin-top: 47px;
  &__items{
    margin-top: 26px;
  }
}


@media screen and (max-width: 1170px){
  .catalog-item{
    &__screen{
      width: 40%;
    }
    &__top,
    &__info{
      padding-right: 41%;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    &__grid{
      grid-template-columns: 100%;
    }
    &__sidebar{
      display: none;
    }
  }

  .about-service{
    margin-top: 52px;
    &__hint{
      margin-top: 7px;
      font-size: 12px;
    }
    &__text{
      margin-top: 14px;
      font-size: 12px;
      p{
        &:not(:last-child){
          margin-bottom: 12px;
        }
      }
    }
  }
}


@media screen and (max-width: 768px){
  .catalog-item{
    padding: 20px 0 70px;
    &__content{
      width: calc(100% + 40px);
      margin-left: -20px;
      padding: 15px 20px 65px 35px;
    }
    &__top{
      padding-right: 0;
      justify-content: space-between;
    }
    &__title{
      font-weight: 600;
      font-size: 15px;
      line-height: 18px;
      text-transform: uppercase;
    }
    &__info{
      margin-top: 5px;
      padding-right: 0;
      grid-template-columns: 100%;
      position: static;
      display: block;
      &-top{
        display: grid;
        grid-template-columns: 1.05fr 1fr;
        gap: 16px;
      }
      &-left{
        display: block;
        grid-column: unset;
      }
      .catalog-card__status{
        margin-top: 10px;
      }
      .catalog-card__status-item{
        &:not(:last-child){
          margin-bottom: 10px;
        }
      }
    }
    &__screen{
      position: static;
      width: 100%;
      padding-top: 13px;
    }
    &__site{
      display: inline-flex;
    }
    &__rating{
      margin-top: 10px;
    }
    &__card{
      padding-bottom: 38px;
      .catalog-card__place{
        width: 25px;
        height: 159px;
        top: 0;
        left: 0;
        right: unset;
        transform: translate(-35px,-15px);
      }
    }
    &__share{
      position: absolute;
      bottom: 0;
      right: 11px;
      &-title{
        font-size: 14px;
      }
    }
    &__hint{
      margin-top: 20px;
      max-width: calc(100% - 11px);
    }
  }

  .product-detail{
    &__group{
      &:not(:last-child){
        margin-bottom: 20px;
      }
    }
    &__text{
      font-size: 12px;
    }
  }

  .add-feedback{
    &__form{
      margin-top: 10px;
    }
    &__buttons{
      margin: 0;
    }
  }
}
