.error {
  padding: 120px 0 225px;
  &__inner {
   position: relative;
  }
  &__img {
    position: absolute;
    top: -40px;
    right: 100px;
    max-width: 356px;
    img {
      width: 100%;
      height: auto;
    }
  }
  &__subtitle {
    font-size: 15px;
    line-height: 150%;
    opacity: 0.5;
    color: $black;
  }
  &__404 {
    font-family: $font1;
    font-weight: 700;
    font-size: 170px;
    line-height: 120%;
    color: $primary;
  }
  &__desc {
    p  {
      font-size: 15px;
      line-height: 150%;
      opacity: 0.7;
      color: $black;
      br {
        display: none;
      }
     a {
       text-decoration: underline;
     }
    }
    p:not(:first-child) {
      margin-top: 5px;
    }
  }
}

@media screen and (max-width: 992px) {
    .error {
      padding: 15px 0 205px;
      &__img {
        position: static;
        margin-right: auto;
        margin-left: auto;
        img {

        }
      }
      &__subtitle {
        margin-top: 30px;
      }
      &__404 {
        font-size: 111px;
      }
      &__desc {
        p {
          br {
            display: block;
          }
          &:not(:first-child) {
            margin-top: 15px;
          }
        }
      }
    }
}

@media screen and (max-width: 576px) {
  .error {
    &__img {
      max-width: 223px;
    }
  }
}
